'use client'

import React, { useState } from 'react'

interface Props {
  onClose: () => void
  onRegistrar: (venta: any) => void
}

export default function ModalRegistroServicio({ onClose, onRegistrar }: Props) {
  const [cliente, setCliente] = useState('')
  const [tecnico, setTecnico] = useState('')
  const [servicio, setServicio] = useState('')
  const [costo, setCosto] = useState('')
  const [descuento, setDescuento] = useState('0')

  const [productos, setProductos] = useState<any[]>([])
  const [prodNombre, setProdNombre] = useState('')
  const [prodCantidad, setProdCantidad] = useState(1)
  const [prodPrecio, setProdPrecio] = useState(0)

  const agregarProducto = () => {
    if (!prodNombre || prodCantidad <= 0 || prodPrecio <= 0) return
    setProductos([...productos, {
      nombre: prodNombre,
      cantidad: prodCantidad,
      precioTotal: prodCantidad * prodPrecio
    }])
    setProdNombre('')
    setProdCantidad(1)
    setProdPrecio(0)
  }

  const registrar = () => {
    const nuevaVenta = {
      idVentaServicio: Date.now(),
      cliente: { idCliente: 1, nombre: cliente },
      tecnico: { idUsuario: 1, nombre: tecnico },
      descuento: parseFloat(descuento),
      costoTotal: parseFloat(costo),
      fechaRegistro: new Date().toISOString(),
      detallesServicio: [{
        idDetalleServicio: 1,
        servicio: { idServicio: 1, nombre: servicio, costo: parseFloat(costo) },
        costoTotal: parseFloat(costo),
        descuento: parseFloat(descuento)
      }],
      productos: productos.map((p, i) => ({
        idProductoDetalleVenta: i + 1,
        producto: { idProducto: i + 1, nombre: p.nombre },
        cantidad: p.cantidad,
        precioTotal: p.precioTotal
      }))
    }

    onRegistrar(nuevaVenta)
  }

  return (
    <div className="modal-overlay">
      <div className="modal-box">
        <div className="modal-header">
          <h2>üìù Registrar Servicio</h2>
          <button onClick={onClose} className="close-btn">‚úñ</button>
        </div>

        <div className="modal-content grid gap-3 mt-4">
          <input className="search-input" value={cliente} onChange={(e) => setCliente(e.target.value)} placeholder="Nombre del Cliente" />
          <input className="search-input" value={tecnico} onChange={(e) => setTecnico(e.target.value)} placeholder="T√©cnico Responsable" />
          <input className="search-input" value={servicio} onChange={(e) => setServicio(e.target.value)} placeholder="Servicio Realizado" />
          <input className="search-input" type="number" value={costo} onChange={(e) => setCosto(e.target.value)} placeholder="Costo" />
          <input className="search-input" type="number" value={descuento} onChange={(e) => setDescuento(e.target.value)} placeholder="Descuento" />

          <div className="border rounded p-3 bg-gray-50">
            <h4 className="font-semibold mb-2">‚ûï Productos (opcional)</h4>
            <div className="grid grid-cols-3 gap-2 mb-2">
              <input className="search-input" placeholder="Nombre" value={prodNombre} onChange={(e) => setProdNombre(e.target.value)} />
              <input className="search-input" type="number" placeholder="Cantidad" value={prodCantidad} onChange={(e) => setProdCantidad(+e.target.value)} />
              <input className="search-input" type="number" placeholder="Precio" value={prodPrecio} onChange={(e) => setProdPrecio(+e.target.value)} />
            </div>
            <button className="primary-btn" onClick={agregarProducto}>Agregar Producto</button>

            <ul className="mt-2 list-disc pl-5">
              {productos.map((p, i) => (
                <li key={i}>{p.nombre} x{p.cantidad} ‚Äî S/ {p.precioTotal}</li>
              ))}
            </ul>
          </div>
        </div>

        <div className="modal-actions mt-4">
          <button className="primary-btn" onClick={registrar}>‚úÖ Registrar</button>
        </div>
      </div>
    </div>
  )
}
